/* src/App.css */

.App-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  /* Dark Blue Header background */
  background-color: var(--primary-color); 
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  position: sticky;
  top: 0;
  z-index: 1000;
  color: var(--text-color-light); /* White text for header */
}

.App-header h1 {
  color: var(--text-color-light); /* Ensure title is white */
  font-size: 24px;
}

.App-title-group {
  display: flex;
  align-items: center;
}

.App-logo-spot {
  width: 40px;
  height: 40px;
  background-color: var(--secondary-color); /* Light blue accent for logo */
  margin-right: 15px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-main {
  max-width: 1000px;
  margin: 40px auto; 
  padding: 0 20px;
}

.Page-content {
    background-color: var(--card-bg-color); 
    padding: 30px;
    border-radius: 12px; 
    box-shadow: var(--shadow-light);
    min-height: 400px;
}

.Page-content h2 {
    margin-bottom: 20px;
    color: var(--primary-color);
    font-size: 28px;
}
