/* src/Navigation.css */

.Nav-link {
  /* White text on dark blue background */
  color: var(--text-color-light); 
  text-decoration: none;
  margin-left: 25px;
  font-weight: 500;
  padding: 5px 0;
  position: relative;
  transition: color 0.3s;
}

/* Underline effect animation */
.Nav-link::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 0;
  /* Light blue underline */
  background-color: var(--secondary-color); 
  transition: width 0.3s ease-out;
}

.Nav-link:hover {
  /* Light blue text on hover */
  color: var(--secondary-color); 
}

.Nav-link:hover::after {
  width: 100%;
}
